<template>
    <div id="secure">
        <v-dialog/>
        <h1>expenditure</h1>
        <input type="text" name="payment_method" v-model="input.payment_method" placeholder="Payment Method" />
        <input type="text" name="price" v-model="input.price" placeholder="Price" />
        <input type="text" name="unit" v-model="input.unit" placeholder="Unit" />
        <input type="text" name="detail" v-model="input.detail" placeholder="Detail" />
        <button type="button" v-on:click="submit()">Submit</button>
    </div>
</template>

<script>
    export default {
        name: 'Secure',
        data() {
            return {
                input: {
                    payment_method: "",
                    price: "",
                    unit: "",
                    detail: ""
                }
            }
        },
        methods: {
            submit() {
                this.$modal.show('dialog', {
                    title: 'Alert!',
                    text: 'Do you want to submit?',
                    buttons: [
                        {
                            title: 'Submit',
                            handler: () => {
                                // console.log(this.input.payment_method);
                                this.axios.post('/submit', {
                                    payment_method: this.input.payment_method,
                                    price: this.input.price,
                                    unit: this.input.unit,
                                    detail: this.input.detail
                                }).then(function(result) {
                                    console.log("haha")
                                }).catch(error => {
                                    alert("Network Error!");
                                });
                            }
                        },
                        {
                            title: 'Close'
                        }
                    ]
                });
            }
        }
    }
</script>

<style scoped>
    #secure {
        width: 500px;
        border: 1px solid #CCCCCC;
        background-color: #FFFFFF;
        margin: auto;
        margin-top: 200px;
        padding: 20px;
    }
</style>